<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">主题标签</span>
          </div>
          <div class="chart-main">
            <themeLabelList
              :labelData="labelData"
              :pageConfig="labelPageConfig"
              @changePage="changePage"
              :showDetailData="labelDetailData"
              @clickDetail="getLabelDetailData"
              @changeDetailPage="changePage"
              @exportExcel="themeLabelExportExcel"
            ></themeLabelList>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">主题推荐关联关系</span>
          </div>
          <div class="chart-main">
            <bdaTable
              :tableData="recommendData"
              :headerColor="'#1899fa'"
              :indexWidth="0"
              :isPage="true"
              :pageConfig="recommendPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span>用户日电量预警</span>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="batteryWarningRes"
            :barChartsData="batteryWarningData"
            :barWidth="40"
            :barType="'bar'"
            :isShadow="true"
            :name="'用户日电量预警'"
            @setOption="setOption"
            @clickBar="getBatteryWarningDetailData"
            :showDetail="true"
            :showDetailData="batteryWarningDetailData"
            @changePageData="changePage"
            :maxShowNumber="13"
          ></barCharts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/electricity-behavior';
import barCharts from '@/components/bar-charts/bar-charts';
import bdaTable from '@/components/bda-table/bda-table'
import themeLabelList from '@/components/theme-label-list/theme-label-list'
import { exportExcelGet } from "@/utils/export";

export default {
  components: {
    barCharts,
    bdaTable,
    themeLabelList
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: '7',
        current: 1,
        size: 10,
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: '7',
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      // 主题推荐关联关系
      recommendData: {},
      recommendRes: false,
      recommendForm: {
        menuType: '7',
        current: 1,
        size: 10,
      },
      //分页设置
      recommendPageConfig: {
        name: 'recommend',
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //用户日电量预警
      batteryWarningData: [],
      batteryWarningRes: false,
      batteryWarningDetailForm: {
        name: "",
        current: 1,
        size: 10,
      },
      batteryWarningDetailData: {},
      //图表组件数据
      setOptionData: {},
    }
  },
  mounted() {
    this.getLabelData()
    this.getRecommendData()
    this.getBatteryWarningData()
    window.addEventListener("resize", () => {
      if (this.setOptionData['用户日电量预警']) this.setOptionData['用户日电量预警'].resize()
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },

    //改变分页
    changePage(data) {
      console.log(data, 'data')
      if (data.name === 'label') {
        this.labelQueryForm.size = data.size
        this.labelQueryForm.current = data.current
        this.getLabelData()
      }
      if (data.name === 'labelDetail') {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || ''
        this.labelDetailQueryForm.size = data.size
        this.labelDetailQueryForm.current = data.current
        this.getLabelDetailData()
      }
      if (data.name === 'recommend') {
        this.recommendForm.size = data.size
        this.recommendForm.current = data.current
        this.getRecommendData()
      }
      if (data.name === 'batteryWarningDetail') {
        this.batteryWarningDetailForm.size = data.size
        this.batteryWarningDetailForm.current = data.current
        this.getBatteryWarningDetailData()
      }
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      exportExcelGet("/leanSeatingSkillController/exportLabelUserDisplay" + "?tagId=" + data.tagId);
    },
    // 获取关注标签
    getLabelData() {

      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total
        this.labelPageConfig.size = this.labelQueryForm.size
        this.labelPageConfig.current = this.labelQueryForm.current
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0,
          }
        })

        console.log(this.labelData, 'labelData')

      })
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId
        this.labelDetailQueryForm.size = 10
        this.labelDetailQueryForm.current = 1
      }
      let columnNameData = []
      this.labelUserDisplay({ ... this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0] ? res.result[0].total : 0
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId].map(e => {
            return {
              prop: e.dbcolumnAS,
              label: e.dbcolumnTitle,
            }
          }).filter(e => e.label !== '地区编码' && e.label !== '县区局编码' && e.label !== '供电单位编码' && e.label !== '供电电网编码')
        }
        console.log(columnNameData, 'columnNameData')
        res.result.shift()
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: '#1899fa',
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size,

        }
        // console.log(this.labelDetailData, 'labelDetailData')

      })
    },
    // 获取主题推荐关联关系
    getRecommendData() {
      let columnNameData = [
        { prop: "clbh", label: "关系编号", },
        { prop: "clmc", label: "关联关系名称", },
        { prop: "clsm", label: "关联关系描述" },
      ]
      this.topicsRecommendAssociations(this.recommendForm).then(res => {
        console.log(res, 'topicsRecommendAssociations')
        this.recommendPageConfig.total = res.total
        this.recommendPageConfig.size = this.recommendForm.size
        this.recommendPageConfig.current = this.recommendForm.current
        this.recommendData = {
          tableData: [...res.records],
          columnNameData: columnNameData
        }
      }).catch(err => {
        if (err) {
          this.recommendData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })
    },
    // 用户日电量预警
    getBatteryWarningData() {
      this.batteryWarningRes = false
      this.dailyElectricWarningUser({}).then(res => {
        console.log(res, 'dailyElectricWarningUser')
        let dataMap = []
        let xNameData = []
        res.forEach(e => {
          dataMap.push(e.count)
          xNameData.push(e.name)
        });
        this.batteryWarningData = {
          dataMap: {
            '用户日电量预警': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '用户日电量预警': [
              ['#2790FF', '#55BFFF', '#dcf1ff']
            ]
          },
          xyName: { 'x轴': { name: '', color: "#4D4D4D", diffcolor: false }, 'y轴': [{ name: '客户数', color: '#808080' }] }
        }
        this.batteryWarningRes = true
      })

    },

    // 获取用户日电量预警详情
    getBatteryWarningDetailData(data) {
      console.log(data, 'getUseUsageDetailData');
      this.batteryWarningDetailData = {}
      if (data) {
        this.batteryWarningDetailForm.name = data.name;
        this.batteryWarningDetailForm.current = 1
        this.batteryWarningDetailForm.size = 10
      }
      this.dailyElectricWarningUserDetails({ detailsContent: this.batteryWarningDetailForm.name, ...this.batteryWarningDetailForm }).then(res => {
        this.batteryWarningDetailData = {
          tableData: [...res.records],
          headerColor: '#1899fa',
          title: '日用电量预警',
          name: 'batteryWarningDetail',
          size: this.batteryWarningDetailForm.size,
          current: this.batteryWarningDetailForm.current,
          total: res.total,
          columnNameData: [

            { prop: "gdj", label: "供电局" },
            { prop: "gdqj", label: "供电区级" },
            { prop: "yhbh", label: "用户编号" },
            { prop: "yhmc", label: "用户名称" },
            { prop: "dqdl", label: "当前电量" },
            { prop: "hbdl", label: "环比电量" },
            { prop: "tbdl", label: "同比电量" },
            { prop: "mom", label: "环比电量" },
            { prop: "yoy", label: "同比电量" },
          ]
        }
      })
    },
  },
}
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  background: #e9f3fe;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  .top,
  .bottom {
    width: 100%;
    // height: calc(50% - 10px);
    height: 300px;
    box-sizing: border-box;
    .content-border {
      background: #fff;
      border: 2px solid rgba(26, 132, 244, 0.2);
      border-radius: 4px;
    }
    .content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      .content-title {
        display: flex;
        align-items: center;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 30px);
        padding: 10px;
        box-sizing: border-box;
      }
      .content-left {
        width: calc(50% - 10px);
        margin-right: 10px;
        height: 100%;
      }
      .content-right {
        width: calc(50% - 10px);
        height: 100%;
      }
    }
  }
  .top {
    margin-bottom: 20px;
  }
  .bottom {
    margin-bottom: 20px;
  }
}
</style>